import React, {Component} from 'react'
import styled from "styled-components"
import Helmet from "react-helmet"
import Header from "../components/header"
import Footer from "../components/footer"
import SEO from "../components/seo"
import ScrollAnimation from 'react-animate-on-scroll'
import { DiPython, } from "react-icons/di";


const MainContainer = styled.div`
	background: linear-gradient(220deg, #0f2027, #203a43, #2c5364);
	background-size: 300% 300%;

	-webkit-animation: Gradient 7s ease infinite;
	-moz-animation: Gradient 7s ease infinite;
	animation: Gradient 7s ease infinite;

	@-webkit-keyframes Gradient {
		0%{background-position:92% 0%}
		50%{background-position:9% 100%}
		100%{background-position:92% 0%}
	}
	@-moz-keyframes Gradient {
		0%{background-position:92% 0%}
		50%{background-position:9% 100%}
		100%{background-position:92% 0%}
	}
	@keyframes Gradient {
		0%{background-position:92% 0%}
		50%{background-position:9% 100%}
		100%{background-position:92% 0%}
	}
`

const SubContainer = styled.div`
	width: 100%;
	height: 100vh;
	position: relative;
	color: white;
	box-sizing: border-box;
`

const CenterContainer = styled.div`
	position: absolute;
	top: 50%;
	transform: translate(0%, -50%);
	width: 100%;
`

const LeftContainer = styled.div`
	display: inline-block;
	width: 50%;
	box-sizing: border-box;

	@media (max-width: 1080px) {
		width: 100%;
		display: block;
		text-align: center;
	}
`

const RightContainer = styled.div`
	display: inline-block;
	width: 50%;
	box-sizing: border-box;
	padding: 20px;
	position: absolute;
	top: 50%;
	transform: translate(0%, -50%);

	@media (max-width: 1080px) {
		width: 100%;
		text-align: center;
		position: relative;
		transform: none;
		display: block;
	}
`

const CircleImageContainer = styled.div`
	width: 160px;
	height: 160px;
	border-radius: 50%;
	margin-right: 80px;
	background-image: url('/profile.jpg');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
	float: right;
	display: inline-block;

	@media (max-width: 1080px) {
		float: none;
		margin: 0;
	}
`

const SubTitleContainer = styled.div`
	font-size: 32px;
	font-family: Montserrat, Noto Sans KR, Helvetica,Arial, sans-serif;
	font-weight: 600;
`

const SubContentContainer = styled.div`
	font-family: Noto Sans KR, Helvetica,Arial, sans-serif;
	@media (min-width: 1080px) {
		max-width: 500px;
	}
`

const LiContainer = styled.div`
	font-family: Montserrat, Noto Sans KR, Helvetica,Arial, sans-serif;
`

const RelativeContainer = styled.div`
	position: relative;
	width: 100%;
`

const InfoContainer = styled.div`
	margin: 20px 0;
`

const InfoTitleContainer = styled.div`
	font-family: Montserrat, Noto Sans KR, Helvetica,Arial, sans-serif;
    font-weight: bold;
    font-size: 20px;
    margin: 4px 0;
`

const InfoTextWrapper = styled.div`
	font-family: Montserrat, Noto Sans KR, Helvetica,Arial, sans-serif;
	margin: 8px 0;
`

export default () => (
	<div>
		<Header />
		<MainContainer>
			<SEO title={`I'm JustKode!`} description={`98년생 학생 프로그래머의 이야기.`} />
			<SubContainer>
				<CenterContainer>
					<RelativeContainer>
						<LeftContainer>
							<div className="animated fadeIn">
								<CircleImageContainer></CircleImageContainer>
							</div>
						</LeftContainer>
						<RightContainer>
							<div className="animated fadeIn delay-1s">
								<SubTitleContainer>I'm justkode!</SubTitleContainer>
								<SubContentContainer>
									<InfoTextWrapper>
										Hello, My Name is <strong>Min Jae Park.</strong><br/>
										I'm an undergraduate student at Kyunghee University majoring in Computer Engineering.
									</InfoTextWrapper>
									<InfoContainer>
										<InfoTitleContainer>Personal Information</InfoTitleContainer>
										<LiContainer>- Born: 1998.07.15</LiContainer>
										<LiContainer>- Favorite Language: <strong>Python</strong>, C++, Javascript</LiContainer>
										<LiContainer>- Email: justkode@kakao.com</LiContainer>
									</InfoContainer>
									<InfoContainer>
										<InfoTitleContainer>I'm Interested in</InfoTitleContainer>
										<LiContainer>- Natural Language Processing</LiContainer>
										<LiContainer>- Big Data Processing</LiContainer>
										<LiContainer>- Cloud Computing</LiContainer>
									</InfoContainer>
									<InfoContainer>
										<InfoTitleContainer>Experience</InfoTitleContainer>
										<LiContainer>- 2017.3 ~ 2019.2: KH Univ. major of C.E Student Council</LiContainer>
										<LiContainer>- 2017.9 ~ 2018.2: KH Univ. Club D.Com, vice president</LiContainer>
										<LiContainer>- 2018.3 ~ 2019.2: KH Univ. Club D.Com, Director of Education</LiContainer>
										<LiContainer>- 2021.3 ~ : KH Univ. major of C.E Student President</LiContainer>
										<LiContainer>- 2021.6 ~ : KH Univ. Software Membership</LiContainer>
									</InfoContainer>
								</SubContentContainer>
							</div>
						</RightContainer>
					</RelativeContainer>
				</CenterContainer>
			</SubContainer>
		</MainContainer>
		<Footer />
	</div>
)